[class*='ui-heading']{display:flex;align-items:center;justify-content:space-between}.ui-heading--title{text-transform:capitalize;line-height:1.2;color:var(--ui-color-white);font-size:calc(var(--ui-font-size-h1) - 20px);font-family:var(--ui-font-family-impact);letter-spacing:2.5px;margin-bottom:var(--ui-space-base-half)}@media (min-width: 580px){.ui-heading--title{font-size:var(--ui-font-size-h1)}}.ui-heading--subtitle{text-transform:capitalize;line-height:1.2;color:var(--ui-color-white);font-family:var(--ui-font-family-poppins-semi-bold);font-size:var(--ui-font-size-h3)}
